// src/components/Orders/OrderForm.js

import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import axios from 'axios';
import {
  Paper,
  Box,
  Typography,
  TextField,
  Button,
  Grid,
  IconButton,
  Autocomplete,
} from '@mui/material';
import DeleteIcon from '@mui/icons-material/Delete';

const OrderForm = () => {
  //   URL: userId => id, orderId ()
  const { id, orderId } = useParams();
  const navigate = useNavigate();

  //   ( /)
  const [orderData, setOrderData] = useState({
    area: '',
    city: '',
    warehouse: '',
    number_phone: '',
    status: '',
    order_number: '',
    products: [],
    totalCost: 0,
  });

  //    
  const [productsList, setProductsList] = useState([]);

  //     
  const [selectedProductId, setSelectedProductId] = useState('');
  const [selectedQuantity, setSelectedQuantity] = useState(1);

  // 1.   
  useEffect(() => {
    axios
      .get('http://13.51.233.141/admin/api/products')
      .then((response) => setProductsList(response.data))
      .catch((error) => console.error('Error fetching products:', error));
  }, []);

  // 2.  orderId  =>  =>   
  useEffect(() => {
    if (orderId) {
      axios
        .get(`http://13.51.233.141/admin/api/users/${id}/orders/${orderId}`)
        .then((response) => {
          setOrderData(response.data); //     
        })
        .catch((error) => console.error('Error fetching order:', error));
    }
  }, [id, orderId]);

  // 3.  
  useEffect(() => {
    const total = orderData.products.reduce((sum, p) => sum + p.cost * p.quantity, 0);
    setOrderData((prevData) => ({ ...prevData, totalCost: total }));
  }, [orderData.products]);

  //     
  const handleOrderChange = (e) => {
    const { name, value } = e.target;
    setOrderData((prevData) => ({
      ...prevData,
      [name]: value,
    }));
  };

  //    
  const handleAddProduct = () => {
    if (!selectedProductId) return;
    const product = productsList.find((p) => p._id === selectedProductId);
    if (!product) return;

    const productEntry = {
      productId: product._id,
      title: product.title,
      color: product.color || '',
      size: product.size || '',
      quantity: Number(selectedQuantity),
      cost: product.cost,
      img: product.imageURL && product.imageURL.length > 0 ? product.imageURL[0] : '',
      sku: product.sku || '',
      id: product.id,
    };

    setOrderData((prevData) => ({
      ...prevData,
      products: [...prevData.products, productEntry],
    }));
    setSelectedProductId('');
    setSelectedQuantity(1);
  };

  //    
  const handleRemoveProduct = (index) => {
    const newProducts = orderData.products.filter((_, i) => i !== index);
    setOrderData((prevData) => ({
      ...prevData,
      products: newProducts,
    }));
  };

  // 4.  (POST  , PUT  )
  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      if (orderId) {
        // 
        await axios.put(`http://13.51.233.141/admin/api/users/${id}/orders/${orderId}`, orderData);
      } else {
        // 
        await axios.post(`http://13.51.233.141/admin/api/users/${id}/orders`, orderData);
      }
      //      
      navigate(`/users/orders/${id}`);
    } catch (error) {
      console.error('Error saving order:', error);
    }
  };

  return (
    <Paper sx={{ padding: 3 }}>
      <Typography variant="h6" gutterBottom>
        {orderId ? `  #${orderId}   ${id}` : `    ${id}`}
      </Typography>
      <Box
        component="form"
        onSubmit={handleSubmit}
        sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}
      >
        {/*    */}
        <Typography variant="subtitle1">  </Typography>
        <TextField
          label=""
          name="area"
          value={orderData.area}
          onChange={handleOrderChange}
        />
        <TextField
          label=""
          name="city"
          value={orderData.city}
          onChange={handleOrderChange}
        />
        <TextField
          label=""
          name="warehouse"
          value={orderData.warehouse}
          onChange={handleOrderChange}
        />
        <TextField
          label=" "
          name="number_phone"
          value={orderData.number_phone}
          onChange={handleOrderChange}
        />

        {/*    */}
        <Typography variant="subtitle1"> </Typography>
        <TextField
          label=" "
          name="order_number"
          value={orderData.order_number}
          onChange={handleOrderChange}
        />
        <TextField
          label=""
          name="status"
          value={orderData.status}
          onChange={handleOrderChange}
        />

        {/*   */}
        <Typography variant="subtitle1"></Typography>
        <Grid container spacing={2} alignItems="center">
          <Grid item xs={5}>
            <Autocomplete
              options={productsList}
              getOptionLabel={(option) => option.title}
              value={productsList.find((product) => product._id === selectedProductId) || null}
              onChange={(event, newValue) => {
                setSelectedProductId(newValue ? newValue._id : '');
              }}
              renderInput={(params) => (
                <TextField {...params} label=" " variant="outlined" fullWidth />
              )}
            />
          </Grid>
          <Grid item xs={3}>
            <TextField
              type="number"
              label=""
              value={selectedQuantity}
              onChange={(e) => setSelectedQuantity(e.target.value)}
            />
          </Grid>
          <Grid item xs={4}>
            <Button variant="contained" onClick={handleAddProduct}>
               
            </Button>
          </Grid>
        </Grid>

        {/*    */}
        {orderData.products.length > 0 && (
          <Box>
            <Typography variant="subtitle1">  </Typography>
            {orderData.products.map((prod, index) => (
              <Box
                key={index}
                sx={{ display: 'flex', alignItems: 'center', gap: 2, mb: 1 }}
              >
                <Typography>
                  {prod.title}  : {prod.quantity}  : {prod.cost}
                </Typography>
                <IconButton onClick={() => handleRemoveProduct(index)} size="small">
                  <DeleteIcon />
                </IconButton>
              </Box>
            ))}
          </Box>
        )}

        <Typography variant="subtitle1"> : {orderData.totalCost}</Typography>

        <Button type="submit" variant="contained" color="primary">
          {orderId ? ' ' : ' '}
        </Button>
      </Box>
    </Paper>
  );
};

export default OrderForm;