// src/components/ProductForm.js
import React, { useState, useEffect } from 'react';
import {
  TextField,
  Button,
  Paper,
  Box,
  Typography,
  FormControl,
  InputLabel,
  Select,
  MenuItem
} from '@mui/material';
import Autocomplete from '@mui/material/Autocomplete';
import { useNavigate, useParams } from 'react-router-dom';
import axios from 'axios';

const ProductForm = () => {
  const { id } = useParams(); //  id    
  const isEditMode = !!id;
  const navigate = useNavigate();

  const [formData, setFormData] = useState({
    id: '',
    title: '',
    category: '',
    categoryLink: '',
    subcategory: '',
    subcategoryLink: '',
    type: '',
    brand: '',
    imageURL: [],
    features: [],       //     ()
    application: [],    //     ()
    description: '',
    costEUR: '',
    sku: '',
    quantity: '',
    discount: 0,
  });

  //  ,   API
  const [categories, setCategories] = useState([]);

  useEffect(() => {
    axios.get('http://13.51.233.141/admin/api/categories')
      .then((response) => setCategories(response.data))
      .catch((error) => console.error('Error fetching categories:', error));
  }, []);

  useEffect(() => {
    if (isEditMode) {
      axios
        .get(`http://13.51.233.141/admin/api/products/${id}`)
        .then((response) => {
          setFormData(response.data);
        })
        .catch((error) => console.error('Error fetching product: ', error));
    }
  }, [id, isEditMode]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    //       value  
    setFormData((prevData) => ({
      ...prevData,
      [name]: value,
    }));
  };

  //    imageURL ()
  const handleImageURLChange = (e, index) => {
    const newImageURLs = [...formData.imageURL];
    newImageURLs[index] = e.target.value;
    setFormData((prevData) => ({
      ...prevData,
      imageURL: newImageURLs,
    }));
  };

  const addImageURLField = () => {
    setFormData((prevData) => ({
      ...prevData,
      imageURL: [...prevData.imageURL, ''],
    }));
  };

  //    features
  const handleFeatureChange = (e, index) => {
    const newFeatures = [...formData.features];
    newFeatures[index] = e.target.value;
    setFormData((prevData) => ({
      ...prevData,
      features: newFeatures,
    }));
  };

  const addFeatureField = () => {
    setFormData((prevData) => ({
      ...prevData,
      features: [...prevData.features, ''],
    }));
  };

  //    application
  const handleApplicationChange = (e, index) => {
    const newApplication = [...formData.application];
    newApplication[index] = e.target.value;
    setFormData((prevData) => ({
      ...prevData,
      application: newApplication,
    }));
  };

  const addApplicationField = () => {
    setFormData((prevData) => ({
      ...prevData,
      application: [...prevData.application, ''],
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      //         ...
      const existingCategory = categories.find(cat => cat.name === formData.category);

      if (existingCategory) {
        //   ,    
        let updatedCategory = { ...existingCategory };

        //      ,  
        if (!updatedCategory.brands || !updatedCategory.brands.includes(formData.brand)) {
          updatedCategory.brands = updatedCategory.brands ? [...updatedCategory.brands, formData.brand] : [formData.brand];
        }

        //   
        const existingSubcategory = updatedCategory.subcategories.find(
          sub => sub.name === formData.subcategory
        );
        if (existingSubcategory) {
          //  :    
          if (!existingSubcategory.brands || !existingSubcategory.brands.includes(formData.brand)) {
            const updatedSubcategories = updatedCategory.subcategories.map(sub => {
              if (sub.name === formData.subcategory) {
                return { 
                  ...sub, 
                  brands: sub.brands ? [...sub.brands, formData.brand] : [formData.brand]
                };
              }
              return sub;
            });
            updatedCategory.subcategories = updatedSubcategories;
          }
        } else {
          //  :      
          const newSubcategory = {
            name: formData.subcategory,
            linkName: formData.subcategoryLink,
            brands: [formData.brand],
          };
          updatedCategory.subcategories = updatedCategory.subcategories 
            ? [...updatedCategory.subcategories, newSubcategory]
            : [newSubcategory];
        }
        
        //    API (PUT)
        await axios.put(`http://13.51.233.141/admin/api/categories/${existingCategory._id}`, updatedCategory);
      } else {
        //      
        const newCategory = {
          name: formData.category,
          linkName: formData.categoryLink,
          //         
          brands: [formData.brand],
          subcategories: [
            {
              name: formData.subcategory,
              linkName: formData.subcategoryLink,
              brands: [formData.brand],
            }
          ],
        };
        await axios.post('http://13.51.233.141/admin/api/categories', newCategory);
      }
  
      //    
      if (isEditMode) {
        await axios.put(`http://13.51.233.141/admin/api/products/${id}`, formData);
      } else {
        await axios.post('http://13.51.233.141/admin/api/products', formData);
      }
      navigate('/products');
    } catch (error) {
      console.error('Error saving product: ', error);
    }
  };

  //       
  const selectedCategory = categories.find(cat => cat.name === formData.category);
  //   ,      subcategories (  )
  const subcategoryOptions = selectedCategory ? selectedCategory.subcategories.map(sub => sub.name) : [];
  //   ,     
  const selectedSubcategory = selectedCategory && selectedCategory.subcategories.find(sub => sub.name === formData.subcategory);
  const brandOptions = selectedSubcategory ? selectedSubcategory.brands : [];

  return (
    <Paper sx={{ padding: 3 }}>
      <Typography variant="h6" gutterBottom>
        {isEditMode ? ' ' : ' '}
      </Typography>
      <Box component="form" onSubmit={handleSubmit} sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
        <TextField label="ID" name="id" value={formData.id} onChange={handleChange} required />
        <TextField label=" " name="title" value={formData.title} onChange={handleChange} required />

        {/*    */}
        <Autocomplete
          freeSolo
          options={categories.map(cat => cat.name)}
          value={formData.category}
          onChange={(event, newValue) => {
            const selected = categories.find(cat => cat.name === newValue);
            setFormData(prevData => ({
              ...prevData,
              category: newValue,
              categoryLink: selected ? selected.linkName : '',
              subcategory: '',
              subcategoryLink: '',
              brand: ''
            }));
          }}
          onInputChange={(event, newInputValue) => {
            //       
            setFormData(prevData => ({
              ...prevData,
              category: newInputValue,
              categoryLink: '',
              subcategory: '',
              subcategoryLink: '',
              brand: ''
            }));
          }}
          renderInput={(params) => (
            <TextField {...params} label="" variant="outlined" />
          )}
        />

        <TextField
          label=" (.)"
          name="categoryLink"
          value={formData.categoryLink}
          onChange={handleChange}
        />

        {/*    */}
        <Autocomplete
          freeSolo
          options={subcategoryOptions}
          value={formData.subcategory}
          onChange={(event, newValue) => {
            const selectedSub = selectedCategory
              ? selectedCategory.subcategories.find(sub => sub.name === newValue)
              : null;
            setFormData(prevData => ({
              ...prevData,
              subcategory: newValue,
              subcategoryLink: selectedSub ? selectedSub.linkName : '',
              brand: ''
            }));
          }}
          onInputChange={(event, newInputValue) => {
            setFormData(prevData => ({
              ...prevData,
              subcategory: newInputValue,
              subcategoryLink: '',
              brand: ''
            }));
          }}
          renderInput={(params) => (
            <TextField {...params} label="" variant="outlined" />
          )}
        />

        <TextField
          label=" (.)"
          name="subcategoryLink"
          value={formData.subcategoryLink}
          onChange={handleChange}
        />

        {/*    */}
        <Autocomplete
          freeSolo
          options={brandOptions}
          value={formData.brand}
          onChange={(event, newValue) => {
            setFormData(prevData => ({ ...prevData, brand: newValue }));
          }}
          onInputChange={(event, newInputValue) => {
            setFormData(prevData => ({ ...prevData, brand: newInputValue }));
          }}
          renderInput={(params) => (
            <TextField {...params} label="" variant="outlined" />
          )}
        />

        {/*       */}
        <FormControl fullWidth>
          <InputLabel id="product-type-label"> </InputLabel>
          <Select
            labelId="product-type-label"
            label=" "
            name="type"
            value={formData.type}
            onChange={handleChange}
          >
            <MenuItem value="sale"></MenuItem>
            <MenuItem value="leaders"> </MenuItem>
          </Select>
        </FormControl>

        <Typography variant="subtitle1"></Typography>
        {formData.imageURL.map((url, index) => (
          <TextField
            key={index}
            label={`Image URL ${index + 1}`}
            value={url}
            onChange={(e) => handleImageURLChange(e, index)}
          />
        ))}
        <Button variant="outlined" onClick={addImageURLField}>
           
        </Button>
        
        {/*       */}
        <Typography variant="subtitle1"></Typography>
        {formData.features.map((feature, index) => (
          <TextField
            key={index}
            label={` ${index + 1}`}
            value={feature}
            onChange={(e) => handleFeatureChange(e, index)}
          />
        ))}
        <Button variant="outlined" onClick={addFeatureField}>
           
        </Button>

        {/*       */}
        <Typography variant="subtitle1"></Typography>
        {formData.application.map((app, index) => (
          <TextField
            key={index}
            label={` ${index + 1}`}
            value={app}
            onChange={(e) => handleApplicationChange(e, index)}
          />
        ))}
        <Button variant="outlined" onClick={addApplicationField}>
           
        </Button>
        
        <TextField
          label=" "
          name="description"
          value={formData.description}
          onChange={handleChange}
          multiline
          rows={4}
        />
        <TextField
          label="  EUR"
          name="costEUR"
          type="number"
          value={formData.costEUR}
          onChange={handleChange}
        />
        <TextField
          label=""
          name="sku"
          value={formData.sku}
          onChange={handleChange}
        />
        <TextField
          label=""
          name="quantity"
          type="number"
          value={formData.quantity}
          onChange={handleChange}
        />
        <TextField
          label=""
          name="discount"
          type="number"
          value={formData.discount}
          onChange={handleChange}
        />
        <Button
          type="submit"
          variant="contained"
          color="primary"
          sx={{ backgroundColor: "#077c8f" }}
        >
          {isEditMode ? ' ' : ' '}
        </Button>
      </Box>
    </Paper>
  );
};

export default ProductForm;
